.side-bar {
    margin: 0;
    padding: 0;
    width: 200px;
    background-color: #181c2e;
    position: fixed;
    height: 100%;
    overflow: auto;
}

.side-bar div {
    background: url('../../assets/cover.png');
    background-color: #181c2e;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 150px;
}

.side-bar div {
    padding-top: 30px;
}

.side-bar div img {
    border-radius: 50%;
    display: block;
    margin: auto;
    width: 90px;
    height: 90px;
    -webkit-filter: drop-shadow(2px 3px 6px #121212);
    filter: drop-shadow(2px 3px 6px #121212);
    object-fit: cover;
}

.side-bar a {
    display: block;
    color: rgba(255, 255, 255, 0.7);
    padding: 16px;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: ease-in-out .35s;
}

.side-bar a svg {
    margin-right: .5em;
}

.side-bar a:hover {
    background-color: #121212;
    color: #FFF;
}

.content {
    margin-left: 200px;
    padding: 1px 16px;
}

@media screen and (max-width: 700px) {
    div.content {
        margin-left: 0;
    }

    .side-bar {
        width: 100%;
        height: auto;
        position: relative;
    }

    .side-bar a {
        float: left;
    }

    .side-bar div {
        display: none;
    }

    .side-bar a svg {
        display: none;
    }
}

@media screen and (max-width: 400px) {
    .side-bar a {
        text-align: center;
    }

    .side-bara svg {
        display: none;
    }
}